@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap" );
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Space Mono', monospace;
}
.dark{
  font-family: 'Space Mono';
font-style: normal;
font-weight: 700;
font-size: 13px;
line-height: 19px;
text-align: right;
letter-spacing: 2.5px;
color: #697C9A;
}
.button1{
  width: 100px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button:hover{
  cursor: pointer;
  color: black;
}
.container{
  display: flex;
  background-color: #F2F2F2;;
  width: 51%;
  justify-content: space-between;
}
.searchDiv{
  width: 51%;
  display: flex;
  background-color: #ffffff;
  height: 69px;
  justify-content: space-around;
      align-items: center;
box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
border-radius: 15px;
}
.searchDiv:hover{
  cursor: pointer;
}
.mainContainer{
  background-color: #F2F2F2;
  width: 100%;
  max-width: 1440px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 24px;
}
.searchImg{
  width: 22px;
  height: 22px;
}
.searchInput{
border: none;
outline: none;
width: 50%;
}
.searchInput::placeholder{
  font-family: 'Space Mono';
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 25px;
color: #4B6A9B;
}
.searchInput:active{
  border: none;
}
.notFound{
  color: red;
  opacity: 0;
}
.button{
  background: #0079FF;
border-radius: 10px;
width: 106px;
height: 50px;
display: flex;
align-items: center;
justify-content: center;
color: white;
}
.button:hover{
  background: #60ABFF;
  color: white;
}
.section{
  width: 51%;
  height: 444px;
  background: #FEFEFE;
box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
border-radius: 15px;
}